a.bttn {
    color: #444;
    text-decoration: none;
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
}

a.bttn:focus,
a.bttn:hover {
    color: #fff;
}

.bttn {
    margin-top: 15px;
    font-size: 14px;
    letter-spacing: 2px;
    text-transform: uppercase;
    display: inline-block;
    text-align: center;
    font-weight: 700;
    padding: 10px 17px;
    border: 2px solid #444;
    border-radius: 2px;
    position: relative;
    -webkit-box-shadow: 0 2px 10px rgba(0,0,0,.16),0 3px 6px rgba(0,0,0,.1);
    box-shadow: 0 2px 10px rgba(0,0,0,.16),0 3px 6px rgba(0,0,0,.1);
}

.bttn:before {
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease;
    position: absolute;
    top: 0;
    left: 50%;
    right: 50%;
    bottom: 0;
    opacity: 0;
    content: "";
    background-color: #00afd7;
    z-index: -2;
}

.bttn:focus:before,
.bttn:hover:before {
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease;
    left: 0;
    right: 0;
    opacity: 1;
}

.call-out-cta .bttn:hover {
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease;
    background-color: #00afd7;
}

// Slick slider
.slick-slide img {
    display: block;
    width: 100%;
    max-width: 900px;
    margin: 0 auto;
}

.slick-prev {
    left: 0;
}

.slick-next {
    right: 0;
}

.slick-dotted.slick-slider {
    margin-bottom: 0;
}

.slick-dots {
    bottom: 30px;
}

.slick-dots li {
    position: relative;
    display: inline-block;
    width: 20px;
    height: 40px;
    margin: 0 5px;
    padding: 0;
    cursor: pointer;
}

.slick-dots li button {
    font-size: 0;
    line-height: 0;
    display: block;
    width: 40px;
    height: 40px;
    padding: 5px;
    cursor: pointer;
    color: transparent;
    border: 0;
    outline: 0;
    background: 0 0;
}

.slick-dots li button:before {
    font-family: slick;
    font-size: 40px;
    line-height: 40px;
    position: absolute;
    top: 0;
    left: 0;
    width: 40px;
    height: 40px;
    content: 'â€¢';
    text-align: center;
    opacity: 0.25;
    color: $c_dark;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.slick-dots li.slick-active button:before {
    opacity: 1;
    color: $c_dark;
}

.slick-slide:focus {
    outline: 0;
}
@media (max-width:992px) {
    .slick-next,
    .slick-prev {
        z-index: 9999;
        // display: none!important;
    }

    .slick-next {
        right: -25px;
    }

    .slick-prev {
        left: -25px;
    }
}
